<template>
            <v-layout row wrap>
                <v-flex
                v-for="curso in Cursos"
                :key="curso"
                xs4
                d-flex
                >
                <v-container>
                 <v-card>
        <v-img
          :src= curso.image
          aspect-ratio="2.75"
        ></v-img>

        <v-card-title primary-title>
          <div>
            <h3 class="headline mb-0">{{ curso.curso_title }}</h3>
            <div> {{ curso.curso_body }} </div>
          </div>
        </v-card-title>

        <v-card-actions>
          <v-btn flat color="blue" @click="Botao1(curso)">AZUL</v-btn>
          <v-btn flat color="red" @click="Botao2(curso)">Verm</v-btn>
        </v-card-actions>
      </v-card>
      </v-container>
                </v-flex>
            </v-layout>
</template>
<style>
</style>
<script>
/* eslint-disable */
export default {
  data() {
    return {
      Id: 0,
      Nome: "",
      Coordenador: "",
      Duracao: 0,
      GradeCurricular: [],
      Cursos: []
    };
  },
  created() {
    this.Cursos = [
      {
        id: "1",
        image:
          "data:image/jpeg;base64,",
        curso_title: "Algo",
        curso_body: "card_text"
      },
      {
        id: "2",
        image:
          "data:image/jpeg;base64,",
        curso_title: "Algo Curso2",
        curso_body: "Eu sou um texto"
      },
      {
        id: "3",
        image:
          "data:image/jpeg;base64,",
        curso_title: "Algo Curso2",
        curso_body: "Eu sou um texto"
      }
    ];
  },
  methods: {
    teste: function() {
      console.log(this.modelo);
    },
    Botao1: function(curso) {
      console.log(curso);
    },
    Botao2: function(curso) {
      console.log(curso);
    }
  }
};
</script>



